body {
    background: #1D3124;
    font-family: 'Delicious Handrawn', cursive;
    font-family: 'Golos Text', sans-serif;
    font-family: 'Inconsolata', monospace;
    margin: 0;
}
.container {
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    text-align: center;
    color: #1D3124;

}

.arvonta {

    padding: 10px;

    background: #E59560;
}

.task-box {
    border: 3px solid #1D3124;

    margin: 15px auto;
    width: 90%;
    background: #E59560;
    color: #1D3124;
    transition: 0.2s;
}

.task {
    text-transform: uppercase;
    color: #F6F4E8;
    display: inline-block;


}

.checkbox {
    margin-left: 15px;
}

.done-task {
    text-decoration: line-through;
    display: inline-block;

}

.done-div {

}

.task-box:hover {
    background: #1D3124;
    color: #F6F4E8;
    border-color: #F6F4E8;
    transition: 0.2s;
    transform: scale(1.05);
}

.checkmark {
    display: inline-block;
    margin-left: 20px;
}


p {
    font-size: 25px;
}

h1 {
    text-transform: uppercase;
    font-size: 30px;

}

button {
    transition: 0.4s;
    width: 40%;
    height: 80px;
    border-radius: 25px;
    border: none;
    padding: 10px;
    font-size: 20px;
    background: #1D3124;
    color: #F6F4E8;
    text-transform: uppercase;
    margin-top: 30px;
    margin-bottom: 30px;
}

button:hover {
    transform: scale(1.2);
    transition: 0.4s;
}

.recent-tasks {
    padding-top: 20px;
    color: #F6F4E8;
    display: flex;
    justify-content: space-evenly;
    background: #E59560;
    display: none;
}

.pet-div {
    background: #1D3124;
    color: #F6F4E8;


}

.pet-div p {
    font-size: 18px;
    padding: 4px;



}

.food {
    padding: 30px 0;
    width: 89%;
    margin: auto;
    border: solid 2px #F6F4E8;
    margin-top: 30px;
    margin-bottom: 30px;


}

.outside {
    padding: 30px 0;
    width: 89%;
    border: solid 2px #F6F4E8;
    margin: auto;
    margin-bottom: 30px;



}

.done-pet-item {
    font-size: 30px;
}

.button-div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 40px;
    margin-left: 10px;
}

.pet-items{
    display: flex;
    align-items: center;
    justify-content: center;

}

.pet-items button {
    font-size: 10px;
    background: #F6F4E8;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    border: 2px solid #E59560;
    margin-left: 10px;

}

@media only screen and (max-width: 800px) {
    .recent-tasks {
        flex-direction: column;
    }
}